import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import { getChangeRate, getCommaNumber } from "../Controller/CoinController";
import "../CSS/CoinBlock.css";

function CoinBlock({ info }) {
  const block = useRef();
  const select = useRef();
  const [firstLoad, setFirstLoad] = useState(true);

  useEffect(() => {
    let thisBlock = select.current;
    let upperList = select.current.parentNode;

    let oneTime = true;
    let codeName;
    if (info.code) codeName = info.code.split("-")[1];
    let downPos = { x: 0, y: 0 };

    const mouseDownHandler = (e) => {
      downPos = { x: e.clientX, y: e.clientY };
    };
    const mouseUpHandler = (e) => {
      if (e.clientX === downPos.x && e.clientY === downPos.y) {
        if (window.location.origin === "https://december-ok.github.io")
          window.location.href =
            window.location.origin + `CoinTrade/#/order/${codeName}`;
        window.location.href = window.location.origin + `#/order/${codeName}`;
      }
    };
    if (codeName && oneTime) {
      oneTime = false;
      thisBlock.addEventListener("mousedown", mouseDownHandler);
      thisBlock.addEventListener("mouseup", mouseUpHandler);
    }
  }, [info.code]);

  useEffect(() => {
    if (!firstLoad) {
      block.current.classList.add("refreshed");
      setTimeout(() => {
        if (block.current) block.current.classList.remove("refreshed");
      }, 1000);
    } else setFirstLoad(false);
  }, [info.trade_price]);
  let hi;
  if (info.code) {
    hi = info.code.split("-")[1];
  }
  return (
    <div
      className="CoinBlock"
      ref={select}
      title={info.code ? info.code.split("-")[1] : ""}
    >
      <div className={`CoinBlock${info.change}`} ref={block} id="cb">
        <div className="content">
          <img
            className="coinImg"
            src={`https://static.upbit.com/logos/${
              info.market.split("-")[1]
            }.png`}
          />
          <span className="koreanName">{info.korean_name}</span>
          <span className="englishName">{info.market.split("-")[1]}</span>
          <div>
            <span className="tradePrice">
              {getCommaNumber(info.trade_price)}
            </span>
            <div>
              <span className="changePrice">
                {info.change === "RISE" ? "▲" : ""}
                {info.change === "FALL" ? "▼" : ""}
                {info.change === "EVEN" ? "0" : ""}
                {getCommaNumber(info.change_price)}
              </span>
              <span className="changePercent">
                {info.change === "RISE" ? "+" : ""}
                {info.change === "FALL" ? "-" : ""}
                {getChangeRate(info.change_rate)}%
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CoinBlock;
