import { useEffect, useRef } from "react";
import { useCoinState } from "../Context/CoinContext";
import CoinBlock from "./CoinBlock";

function CoinBlockList({ sortBy }) {
  const coinState = useCoinState();
  const list = useRef();
  useEffect(() => {
    let thisBlock = list.current;

    let toggle = false;
    let downPos = { x: 0, y: 0 };
    let listPos = 0;

    const mouseDownHandler = (e) => {
      toggle = true;
      downPos = { x: e.clientX, y: e.clientY };
      listPos = thisBlock.scrollLeft;
    };
    const mouseMovehandler = (e) => {
      if (toggle) {
        let dx = e.clientX - downPos.x;
        thisBlock.scrollLeft = listPos - dx;
      } else {
      }
    };
    const mouseLeaveHandler = (e) => {
      toggle = false;
    };
    const mouseUpHandler = (e) => {
      toggle = false;
    };

    thisBlock.addEventListener("mousedown", mouseDownHandler);
    thisBlock.addEventListener("mousemove", mouseMovehandler);
    thisBlock.addEventListener("mouseleave", mouseLeaveHandler);
    thisBlock.addEventListener("mouseup", mouseUpHandler);
  }, []);

  return (
    <div className="CoinBlockList" ref={list}>
      {sortMarkets(sortBy, coinState).map((item, index) => (
        <CoinBlock key={index} info={item} />
      ))}
    </div>
  );
}

export default CoinBlockList;

const sortMarkets = (sortBy, coinState) => {
  let showMarkets = [];
  if (sortBy === "trade_price") {
    showMarkets = coinState.markets
      .sort((a, b) => {
        return b.acc_trade_price_24h - a.acc_trade_price_24h;
      })
      .slice(0, 7);
  } else if (sortBy === "RISE") {
    showMarkets = coinState.markets
      .sort((a, b) => {
        let acomb = a.change_rate;
        let bcomb = b.change_rate;
        if (a.change === "FALL") acomb = -acomb;
        if (b.change === "FALL") bcomb = -bcomb;
        return bcomb - acomb;
      })
      .slice(0, 7);
  } else if (sortBy === "FALL") {
    showMarkets = coinState.markets
      .sort((a, b) => {
        let acomb = a.change_rate;
        let bcomb = b.change_rate;
        if (a.change === "FALL") acomb = -acomb;
        if (b.change === "FALL") bcomb = -bcomb;
        return acomb - bcomb;
      })
      .slice(0, 7);
  } else {
    showMarkets = coinState.markets;
  }

  return showMarkets;
};
